"use client"

import type React from "react"
import { useState } from "react"
import { motion } from "framer-motion"
import Image from "next/image"
import { ChevronLeft, ChevronRight, ExternalLink, Github } from "lucide-react"
import { TechIcon } from "./tech-icon"

type Project = {
  title: string
  description: string
  link: string
  github?: string
  technologies: string[]
  images: string[]
  video: string | null
}

type ProjectCardProps = {
  project: Project
  index: number
  darkMode: boolean
}

export const ProjectCard: React.FC<ProjectCardProps> = ({ project, index, darkMode }) => {
  const [currentImageIndex, setCurrentImageIndex] = useState(0)

  const handlePrevImage = () => {
    setCurrentImageIndex((prev) => (prev - 1 + project.images.length) % project.images.length)
  }

  const handleNextImage = () => {
    setCurrentImageIndex((prev) => (prev + 1) % project.images.length)
  }

  // Animation variants
  const cardVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: (i: number) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: i * 0.1,
        duration: 0.5,
        ease: "easeOut",
      },
    }),
  }

  return (
    <motion.div
      custom={index}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, margin: "-50px" }}
      variants={cardVariants}
      whileHover={{ y: -5 }}
      className={`${darkMode ? "bg-gray-700" : "bg-white"} p-6 rounded-lg shadow-lg hover:shadow-xl transition-all duration-300`}
    >
      <h3 className="text-xl font-semibold mb-2">{project.title}</h3>
      <div className="mb-4 relative aspect-video overflow-hidden rounded-md group">
        <Image
          src={project.images[currentImageIndex] || "/placeholder.svg"}
          alt={project.title}
          width={600}
          height={340}
          className="rounded-md object-cover w-full h-full transition-transform duration-500 group-hover:scale-105"
        />
        {project.images.length > 1 && (
          <>
            <button
              onClick={handlePrevImage}
              className="absolute left-2 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 text-white p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity"
              aria-label="Previous image"
            >
              <ChevronLeft className="w-6 h-6" />
            </button>
            <button
              onClick={handleNextImage}
              className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 text-white p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity"
              aria-label="Next image"
            >
              <ChevronRight className="w-6 h-6" />
            </button>
          </>
        )}
      </div>
      <p
        className={`${darkMode ? "text-gray-300" : "text-gray-600"} mb-4 line-clamp-3 hover:line-clamp-none transition-all duration-300`}
      >
        {project.description}
      </p>
      <div className="flex flex-wrap gap-2 mb-4">
        {project.technologies.map((tech, techIndex) => (
          <div key={techIndex} className="px-2 py-1 bg-blue-500 text-white text-sm rounded flex items-center gap-1">
            <TechIcon name={tech.toLowerCase()} className="w-4 h-4" />
            <span>{tech}</span>
          </div>
        ))}
      </div>
      {project.video && (
        <div className="mb-3">
          <video width="100%" height="200" controls className="rounded-md" preload="metadata">
            <source src={project.video} type="video/mp4" />
            <p>Your browser does not support the video tag.</p>
          </video>
        </div>
      )}
      <div className="flex space-x-4">
        {project.link && project.link !== "#" && (
          <a
            href={project.link}
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-500 hover:text-blue-700 flex items-center gap-1 transition-colors"
          >
            <ExternalLink className="w-4 h-4" />
            Ver Site
          </a>
        )}
        {project.github && (
          <a
            href={project.github}
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-500 hover:text-blue-700 flex items-center gap-1 transition-colors"
          >
            <Github className="w-4 h-4" />
            GitHub
          </a>
        )}
      </div>
    </motion.div>
  )
}

